  plugins: [createPersistedState()],
  mutations: {
    incrementaFolioEneo(state, amount = 1){
      state.folioEneo += Number(amount);
    },
    resetState(state) {
      Object.assign(state.resultadoSeleccionado, getresultadoSeleccionado);
    },
    resetSolState(state) {
      Object.assign(state.solicitudes.selectedSolicitud, getselectedSolicitud);
      state.solicitudes.search = "";
    },
    resetBusqueda(state) {
      state.resultadoSearch = vacioTxt;
    },
    updateResultSelect(state, payload) {
      state.resultadoSeleccionado.fechaInicio = payload.fechaInicio;
      state.resultadoSeleccionado.fechaTermino = payload.fechaFin;
      state.resultadoSeleccionado.tipoMovimiento = payload.tipoMovimiento;
      state.resultadoSeleccionado.tipoContratacion = payload.tipoContra;
      state.resultadoSeleccionado.folioDGP = 'F'+''.padStart(5, "0")+state.folioEneo;
      state.resultadoSeleccionado.division = "SUAyED";
      state.resultadoSeleccionado.cicloEscolar = "2023-1";
    },
    updateSesion(state, payload) {
     state.sesion.usuario = payload.usuario;
     state.sesion.contrasenia = payload.contrasenia;
     state.sesion.nombre = payload.nombre;
     state.sesion.rol = payload.rol;
     state.sesion.rolId = payload.rolId;
    },
    appenMovimiento(state,payload) {
      state.movimientosAcademicos.push(payload);
    }
  },
  actions: {
    addMovimiento({ commit }, payload) {
      commit("appenMovimiento",payload)
    },
    incrementFolio({ commit }){
      commit("incrementaFolioEneo",1);
    },
    resetResultadoSeleccionadoState({ commit }) {
      commit("resetState");
    },
    resetSolSeleccionadoState({ commit }) {
      commit("resetSolState");
    },
    resetResultadoBusquedaState({ commit }) {
      commit("resetBusqueda");
    },
    updateRslSelect({ commit }, payload) {
      commit("updateResultSelect", payload);
    },
    updateSesions({ commit }, payload) {
      commit("updateSesion", payload);
    },
  },
  getters: {
    getMovimientos: (state) => (step) => {
      return state.movimientosAcademicos.filter(mov => mov.step == step);
    },
    getMovimientosFinales: (state) => (step1,step2) => {
      return state.movimientosAcademicos.filter(mov => mov.step == step1 || mov.step == step2);
    },
    login: (state) => (userr, passwordd) => {
      return state.users.filter(user => user.usuario==userr && user.contrasenia==passwordd);
    },
    getClavesMaterias: function (state) {
      return state.database.dbASIG.data.map((item) => item.clave);
    },
    getNombreMaterias: function (state) {
      return state.database.dbASIG.data.map((item) => item.asignatura);
    },
    getNombreMat: (state) => (claveMateria) => {
      var rowMateria = state.database.dbASIG.data.filter(
        (m) => m.clave === claveMateria
      );
      return rowMateria;
    },
    getClaveMat: (state) => (nombreMateria) => {
      var rowMateria = state.database.dbASIG.data.filter(
        (m) => m.asignatura === nombreMateria
      );
      return rowMateria;
    },
    sesion: state => {
      return state.sesion;
    },
    movimientos: state => {
      return state.movimientosAcademicos;
    },
    getSolicitudSelected: state =>{
      return state.solicitudes.selectedSolicitud;
    },
    
    getMovAcademicosSol: state =>{
      return state.solicitudes.selectedSolicitud.movAcademicos.filter( (materia) => materia.claveMat != "" );
    }
    
  },
